const Course = require("../api/models/course");

// Function to get the list of courses
const courseList = async (req, res) => {
  try {
    const courses = await Course.find().exec();
    res.render("courseList", { courses, title: "Courses" }); // Include title for the view
  } catch (error) {
    res.status(500).send("Error retrieving course list: " + error);
  }
};

// Function to get the details of a specific course
const courseDetail = async (req, res) => {
  const courseId = req.params.id;

  try {
    const course = await Course.findById(courseId).exec();
    if (!course) {
      return res.status(404).send("Course not found");
    }
    res.render("courseDetail", { course, title: "Course Details" }); // Include title for the view
  } catch (error) {
    res.status(500).send("Error retrieving course details: " + error);
  }
};

module.exports = {
  courseList,
  courseDetail,
};